::-webkit-scrollbar {
  width: 16px !important;
}

::-webkit-scrollbar-track {
  border-radius: 8px !important;
}

::-webkit-scrollbar-thumb {
  height: 56px !important;
  border-radius: 8px !important;
  border: 4px solid transparent !important;
  background-clip: content-box !important;
  background-color: #888 !important;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #555 !important;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.convertDiv {
  height: 90vh;
  width: 100%;
  background-color: rgb(215, 215, 236);
  display: flex;
  .alertDiv {
    background-color: #252525;
    width: 90%;
    height: 8vh;
    color: white;
    display: flex;
    align-items: center;
    padding: 0 2rem;
    box-sizing: border-box;
    border-radius: 12px;
  }
  .convertInner {
    height: 100%;
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    .resultDiv {
      display: flex;
      align-items: center;
      justify-content: center;
      min-width: 3rem;
      background-color: black;
      padding: 5px 10px;
      color: white;
      border-radius: 20px;
      border: 2px solid rgb(178, 174, 174);
      box-shadow: 0 0 10px black;
    }
    .container {
      height: 50vh;
      width: 30vw;
      display: flex;
      flex-direction: column;
      select {
        box-sizing: border-box;
        border: 1px groove rgba(255, 255, 255, 0.761);
        padding: 2px 0;
        &:focus {
          border: 1px groove rgba(255, 255, 255, 0.761);
          outline: none;
        }
        > option {
          &:hover {
            background-color: red;
          }
        }
      }
      input {
        padding: 2px 5px;
        border: 1px groove rgba(255, 255, 255, 0.761);
        outline: none;
      }
      button {
        margin: 1rem 0;
        outline: none;
        border: none;
        background-color: black;
        color: rgba(255, 255, 255, 0.859);
        padding: 5px 0;
        font-weight: 450;
        letter-spacing: 2px;
        font-size: 1.2rem;
        transition: 0.3s;
        &:hover {
          background-color: rgba(0, 0, 0, 0.834);
          color: rgba(255, 255, 255, 0.859);
        }
      }
    }
  }
  .allCurrencies {
    height: 100%;
    width: 30%;
    display: flex;
    padding: 1rem;
    box-sizing: border-box;
    justify-content: center;
    .currency {
      overflow: auto;
      min-height: 1rem;
      min-width: 5rem;
      background-color: #252525;
      border: 2px solid rgb(178, 174, 174);
      box-shadow: 0 0 10px rgb(0, 0, 0);
      color: rgb(100, 100, 250);
      text-align: center;
      border-radius: 10px;
      font-family: "Roboto", sans-serif;

      span {
        padding: 2px 1rem;
        display: block;
        transition: 0.3s;
        &:hover {
          color: whitesmoke;
          cursor: default;
        }
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .convertDiv {
    padding-top: 2rem;
    min-height: 130vh;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    .resultDiv {
      margin: 2rem 0;
    }
    .convertInner {
      height: 90%;
          justify-content: start;      
      >.container {
      width: 100%;
    }
  }
    .allCurrencies {
      height: 40%;
      width: 80%;
    }
  }
}
